'use client';

import { useState } from 'react';
import Sidebar from "./home/sidebar";
import Navbar from "./home/navbar";

export default function LayoutWrapper({ children }) {
  const [sidebarOpen, setSidebarOpen] = useState(false);

  return (
    <div className="flex min-h-screen bg-white">
      {/* Mobile Sidebar Overlay */}
      {sidebarOpen && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
          onClick={() => setSidebarOpen(false)}
        />
      )}

      {/* Sidebar */}
      <div className={`
        fixed top-0 left-0 h-screen z-50 bg-[#8A9774] transition-transform duration-300 ease-in-out
        w-60 lg:w-60 md:w-56 sm:w-52
        ${sidebarOpen ? 'translate-x-0' : '-translate-x-full lg:translate-x-0'}
      `}>
        <Sidebar sidebarOpen={sidebarOpen} setSidebarOpen={setSidebarOpen} />
      </div>

      {/* Main Area */}
      <div className="flex-1 min-h-screen bg-white lg:ml-60 md:ml-56 sm:ml-52 ml-0">
        {/* Navbar */}
        <div className="fixed top-0 right-0 h-16 lg:h-20 z-40 bg-[#C0C8A4] lg:left-60 md:left-56 sm:left-52 left-0">
          <Navbar sidebarOpen={sidebarOpen} setSidebarOpen={setSidebarOpen} />
        </div>

        {/* Content Area */}
        <div className="pt-16 lg:pt-20 px-3 sm:px-4 md:px-6 lg:px-6">
          {children}
        </div>
      </div>
    </div>
  );
}
